@font-face {
	font-family: 'Inter';
	src:
    url(/static/media/Inter-Regular.3ac83020.woff),
    url(/static/media/Inter-Regular.dc131113.woff2);
  font-weight: normal;
}

@font-face {
  font-family: 'Inter';
	src:
    url(/static/media/Inter-Black.d0b121f3.woff),
    url(/static/media/Inter-Black.661569af.woff2);

	font-weight: 900;
}

@font-face {
  font-family: 'Inter';
	src:
    url(/static/media/Inter-Medium.c0638bea.woff),
    url(/static/media/Inter-Medium.75db5319.woff2);
	font-weight: 500;
}

:root {
  --bg-dark-color:  #000;
  --font-color: #fff;
}

.page {
	background-color: var(--bg-dark-color);
  	min-height: 100vh;
	color: var(--font-color);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	min-width: 320px;
}

.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 880px;
  margin: 0 auto;
  padding-top: 45px;
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: solid rgb(84,84,84,0.7) 1px;
}

@media screen and ( max-width: 768px ) {
  .header{
    flex-direction: column;
  }
}

@media screen and ( max-width: 500px ) {
  .header{
    padding-top: 28px;
    padding-bottom: 31px;
  }
}

@media screen and ( max-width: 920px ) {
  .header__logo{
    margin-left: 20px;
  }
}
@media screen and ( max-width: 768px ) {
  .header__logo{
    margin-bottom: 20px;
    margin-left: 0;
  }
}

@media screen and ( max-width: 500px ) {
  .header__logo{
    width: 104px;
    height: 24px;
  }
}

.header__nav{
    display: flex;
    justify-content: space-between;
}

.header__nav p:not(:last-child) {
    margin-right: 24px;
}

@media screen and ( max-width: 920px ) {
  .header__nav{
    margin-right: 30px;
  }
}
.header__nav-link {
    margin: 0;
    padding: 0;
    font-family: "Inter", Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 18px;
    text-overflow: ellipsis;
    max-width: 500px;
    overflow: hidden;
    white-space: nowrap;
    text-decoration: none;
    color: var(--font-color);
  }
  
  @media screen and ( max-width: 920px ) {
    .header__nav-link{
      max-width: 350px;
    }
  }
  
  @media screen and ( max-width: 425px ) {
    .header__nav-link {
      font-size: 14px;
      max-width: 220px;
    }
  }
  
.header__button {
    background-color: transparent;
    border: none;
    color: #A9A9A9;
    cursor: pointer;
}
.paragraph {
  margin: 0;
  padding: 0;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  text-overflow: ellipsis;
  max-width: 500px;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and ( max-width: 920px ) {
  .paragraph{
    max-width: 350px;
  }
}

@media screen and ( max-width: 425px ) {
  .paragraph {
    font-size: 14px;
    max-width: 220px;
  }
}

.spinner {
    width:60px;
    height: 60px;
}
.spinner__wrapper {
    width: 100%;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.profile {
  max-width: 880px;
  margin: 0 auto;
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and ( max-width: 920px ) {
  .profile{
    margin: 0 20px;
    margin-bottom: 50px;
  }
}


@media screen and ( max-width: 768px ) {
  .profile{
    flex-direction: column;
    margin-bottom: 36px;
  }
}

.profile__content {
  display: flex;
  align-items: center;
}

@media screen and ( max-width: 768px ) {
  .profile__content {
    flex-direction: column;
    margin-bottom: 36px;
  }
}

.profile__avatar-container{
  position: relative;
  margin-right: 30px;
}

.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
}

@media screen and ( max-width: 768px ) {
  .profile__avatar{
    margin-right: 0;
    margin-bottom: 26px;
  }
}

.profile__avatar-overlay{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  transition: .3s ease;
  background-color: rgba(0, 0, 0, 0.8);
}

.profile__avatar-overlay:hover {
  opacity: 1;
  cursor: pointer;
}

.profile__avatar-edit {
  height: 26px;
  width: 26px;
}

.profile__info {
  display: flex;
  flex-direction: column;
}

@media screen and ( max-width: 768px ) {
  .profile__info{
    align-items: center;
  }
}

.profile__info-main{
  display: flex;
  align-items:baseline;
  margin-bottom: 16px;
}

@media screen and ( max-width: 425px ) {
  .profile__info-main {
    margin-bottom: 14px;
  }
}

.profile__info-name {
  margin: 0;
  margin-right: 18px;
  padding: 0;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  max-width: 500px;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and ( max-width: 920px ) {
  .profile__info-name{
    max-width: 350px;
  }
}


@media screen and ( max-width: 500px ) {
  .profile__info-name {
    margin-right: 10px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}

@media screen and ( max-width: 425px ) {
  .profile__info-name {
    max-width: 220px;
  }
}

.profile__info-edit{
  margin: 0;
  padding: 0;
  background: url(/static/media/edit-btn-icon.6bfdb354.svg) center no-repeat;
  border: solid 1px var(--font-color);
  width: 24px;
  height: 24px;
  transition: opacity 0.3s;
  cursor: pointer;
}

.profile__info-edit:hover{
  opacity: 0.6;
}

@media screen and ( max-width: 500px ) {
  .profile__info-edit{
    width: 18px;
    height: 18px;
    background-size: 7.5px 7.5px;
  }
}

.profile__add-btn{
  background: url(/static/media/add-btn-icon.b2fce4ac.svg) center no-repeat;
  border: solid 2px var(--font-color);
  border-radius: 2px;
  width: 150px;
  height: 50px;
  cursor: pointer;
  transition: opacity 0.3s;
}

.profile__add-btn:hover{
  opacity: 0.6;
}

@media screen and ( max-width: 768px ) {
  .profile__add-btn{
    width: 100%;
    margin: 0 auto;
    background-size: 16px 16px;
  }
}

.name{
  margin: 0;
  padding: 0;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: var(--bg-dark-color);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}


.name_place_popup{
  margin-bottom: 54px;
}

@media screen and ( max-width: 768px ) {
  .name_place_popup{
    font-size: 18px;
    line-height: 1.1;
  }
}

@media screen and ( max-width: 420px ) {
  .name_place_popup{
    margin-bottom: 75px;
  }
}

.name_place_register {
    color: var(--font-color);
    text-align: center;
    margin-bottom: 50px;
  }
.name_place_tooltip {
  width: 358px;
  text-overflow:unset;
  white-space: unset;
  overflow: visible;
  text-align: center;
}
.elements{
  display: grid;
  max-width: 880px;
  margin: 0 auto;
  margin-bottom: 66px;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 17px;
  -webkit-column-gap: 17px;
          column-gap: 17px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

@media screen and ( max-width: 920px ) {
  .elements{
    grid-template-columns: repeat(2, 1fr);
    margin: 0 20px;
    margin-bottom: 66px;
  }
}

@media screen and ( max-width: 500px ) {
  .elements{
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    row-gap: 20px;
    margin-bottom: 48px;
  }
}

.card{
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 282px;
  border-radius: 10px;
  background-color: var(--font-color);
}

@media screen and ( max-width: 920px ) {
  .card{
    width: 100%;
  }
}

@media screen and ( max-width: 320px ) {
  .card{
    width: 282px;
  }
}

.card__img{
  width: 282px;
  height: 282px;
  object-fit: cover;
}

@media screen and ( max-width: 920px ) {
  .card__img {
    width: 100%;
  }
}

.card__content{
  display: flex;
  align-items: center;
  padding: 25px 15px 22px 21px;
  justify-content: space-between;
}

.card__likes-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 3px;
  gap: 3px;
}

.card__like-btn{
  padding: 0;
  background: url(/static/media/like-btn.00b1c22a.svg) center no-repeat;
  border: none;
  width: 22px;
  height: 19px;
  cursor: pointer;
  flex: none;
  transition: opacity 0.3s;
}

.card__like-btn:hover{
  opacity: 0.5;
}

.card__like-btn_state_active{
  background: url(/static/media/like-btn__active.302bdf76.svg) center no-repeat;
}

.card__delete-button{
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 0;
  background: url(/static/media/delete.cd42fe98.svg) center no-repeat;
  border: none;
  width: 18px;
  height: 19px;
  cursor: pointer;
  flex: none;
  transition: opacity 0.3s;
}

.card__delete-button:hover{
  opacity: 0.6;
}

.card__delete-button_hidden{
    visibility: hidden;
}
.card__delete-button_visible {
    visibility: visible;
}
.card__like-count {
  color: var(--bg-dark-color);
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-size: 13px;
  text-align: center;
}

.footer{
  max-width: 880px;
  margin: 0 auto;
  padding-bottom: 60px;
}

@media screen and ( max-width: 920px ) {
  .footer{
    width: calc( 100% - 40px );
    padding-bottom: 36px;
  }
}

.footer__copyright{
  margin: 0;
  padding: 0;
  color:#545454;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.1;
}

@media screen and ( max-width: 768px ) {
  .footer__copyright{
    font-size: 14px;
  }
}

.popup {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.2s linear;
}

.popup_type_img-zoom {
  background-color: rgba(0, 0, 0, 0.9);
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 430px;
}

@media screen and ( max-width: 590px ) {
  .popup__container {
    min-width: 240px;
    max-width: calc(100% - 40px);
  }
}

.popup__container_type_form {
  background-color: var(--font-color);
  padding: 34px 37px 36px;
  border-radius: 10px;
}

@media screen and ( max-width: 768px ) {
  .popup__container_type_form {
    padding: 25px 22px;
  }
}

@media screen and ( max-width: 590px ) {
  .popup__container_type_form {
    min-width: 430px;
    max-width: calc(100% - 40px);
  }
}

@media screen and ( max-width: 480px ) {
  .popup__container_type_form {
    min-width: calc(100% - 40px);
  }
}

@media screen and ( max-width: 320px ) {
  .popup__container_type_form {
    min-width: 282px;
  }
}

.popup__container_type_tooltip {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 36px;
}
.popup__close-btn{
  position: absolute;
  width: 32px;
  height: 32px;
  top: -48px;
  right: -48px;
  background: url(/static/media/close-icon.cb0f2bbf.svg) center no-repeat;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s;
}

.popup__close-btn:hover {
  opacity: 0.6;
}

@media screen and ( max-width: 768px ) {
  .popup__close-btn{
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
    top: -36px;
    right: 0;
  }
}

.popup__fieldset{
  border: none;
  margin: 0;
  padding: 0;
}

.popup__input{
  display: block;
  border: none;
  border-bottom: solid 1px rgb(0, 0, 0, 0.2);
  width: 100%;
  padding: 0;
  padding-bottom: 10px;
  margin-bottom: 5px;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1;
}

.popup__input:last-child {
  margin-bottom: 48px;
}

@media screen and ( max-width: 768px ) {
  .popup__input:last-child {
    margin-bottom: 45px;
  }
}

.popup__input_type_error {
  border-bottom: 1px solid #FF0000;
}

.popup__input_place-register {
  background-color: transparent;
  border-bottom: solid 1px #CCCCCC;
  color:var(--font-color);
  margin-bottom: 30px;
}
.popup__submit {
  width: 100%;
  background-color: var(--bg-dark-color);
  color: var(--font-color);
  border: none;
  height: 50px;
  border-radius: 2px;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  transition: opacity 0.3s;
  cursor: pointer;
}

.popup__submit:hover{
  opacity: 0.8;
}

@media screen and ( max-width: 768px ) {
  .popup__submit {
    height: 46px;
    font-size: 14px;
  }
}

.popup__submit_place-register {
    background-color: var(--font-color);
    color: var(--bg-dark-color);
    margin-top: 216px;
    margin-bottom: 15px;
  }
  
  .popup__submit_place-register:hover{
    opacity: 0.85;
  }

  @media screen and ( max-width: 920px ) {
    .popup__submit_place-register {
      margin-top: 170px;
    }
  }
.popup__submit_disabled {
  background-color: transparent;
  color: #c5c5c5;
  border: solid 1px #c5c5c5;
}

.popup__zoom-img{
  max-width: 75vw;
  max-height: 75vh;
}

@media screen and ( max-width: 590px ) {
  .popup__zoom-img{
    max-width: 100%;
  }
}

.popup__description {
  margin: 0;
  padding: 0;
  color: var(--font-color);
  margin-top: 10px;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 12px;
  line-height: 1.25;
}

.error-message {
  display: block;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 12px;
  line-height: 1.2;
  color: #FF0000;
  margin-bottom: 13px;
  max-width: 356px;
  height: 30px;
}

.error-message:last-child {
  margin-bottom: 32px;
}

.tooltip__image {
